import axios from 'axios'
const baseUrl = '/api/about'

const aboutPages = [
  {
    title: 'Vin?',
    imgSrc: '',
    content: 'Vin on korttipeli!'
  },
  {
    title: 'Pelin aloittaminen',
    imgSrc: '',
    content: 'Jaa kortit'
  },
  {
    title: 'Pelin voittaminen',
    imgSrc: '',
    content: 'Ker vhiten pisteit'
  }
]

const getAboutPageData = async () => {
  const { data } = await axios.get(baseUrl)
  return data
}

export default {
  aboutPages,
  getAboutPageData
}


